/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import { Col, Container, Form, Row, Card, Button, Spinner } from 'react-bootstrap';
import { useFormik } from 'formik';
import SelectPhoneCodeInput from 'components/input/SelectPhoneCodeInput';
import TextIconInput from 'components/input/TextIconInput';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import RadioButton from 'components/input/RadioButton';
import RadioInputRounded from 'components/input/RadioInputRounded';
import CheckboxInput from 'components/input/CheckboxInput';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { getContactDetail, updateContact } from 'app/basicInformationSlice/basicInformationSlice';
import Cookies from 'js-cookie';
import { BoolToString, formatBoolean } from 'components/helper/helper';
import { useHistory } from 'react-router';
import { routers } from 'router/router';

interface Props {}

const ContactDetailPage = (props: Props) => {
  const dispatch = useAppDispatch();
  const basicInformationStore = useAppSelector((store) => store.basicInformation);
  const [alternativePhone, setAlternativePhone] = React.useState(true);
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      country_code: undefined,
      phone_number: undefined,
      phone_number_type: undefined,
      is_have_alternative_phone_number: 'true',
      alternative_country_code: undefined,
      alternative_phone_number: undefined,
      alternative_phone_number_type: undefined,
      is_allowed_share_contact: true,
    },
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        is_have_alternative_phone_number: formatBoolean(values.is_have_alternative_phone_number),
        student_id: parseInt(studentId || ''),
      };
      const responese = await dispatch(updateContact(newValues));
      if (responese.payload) history.push(routers.demographics.path);
    },
  });
  const studentId = Cookies.get('userId');
  React.useEffect(() => {
    setAlternativePhone(formik.values.is_have_alternative_phone_number === 'true');
  }, [formik.values.is_have_alternative_phone_number]);
  const getDetail = async () => {
    let res = await dispatch(getContactDetail(studentId));
    const dataLoaded: any = res.payload;
    const values: any = {
      ...dataLoaded,
      is_have_alternative_phone_number: BoolToString(dataLoaded.is_have_alternative_phone_number),
    };
    formik.setValues(values);
  };
  React.useEffect(() => {
    getDetail();
  }, []);
  return (
    <div>
      <TopProcessBar
        stepProcess={[]}
        title="Personal Information"
        process={1}
        icon={<PersonOutlineOutlinedIcon />}
      />

      <Container className="my-4">
        <Form style={{ maxWidth: '1000px' }} onSubmit={formik.handleSubmit}>
          <div className="text-muted fs-3 fw-bold my-3">Contact Details</div>
          <div className="fs-5">What is your phone number?</div>
          <div className="text-muted">
            This address is your permanent address. It’s most likely where your parents or legal
            guardians live. If you’re an international student, please enter your home country
            address.
          </div>

          <Row>
            <Col md={8}>
              <Row className="p-2">
                <SelectPhoneCodeInput
                  label="Country Code"
                  name="country_code"
                  onChange={formik.handleChange}
                  values={formik.values.country_code}
                />
                <TextIconInput
                  icon={<PhoneAndroidIcon />}
                  name="phone_number"
                  onChange={formik.handleChange}
                  values={formik.values.phone_number}
                  label="Phone Number"
                />
              </Row>
              <Form.Group
                style={{ marginTop: '-16px' }}
                as={Row}
                name={'phone_number_type'}
                values={formik.values.phone_number_type}
                onChange={formik.handleChange}
              >
                <Col md={6}>
                  <RadioButton
                    groupValue={formik.values.phone_number_type}
                    value={'home'}
                    label={'Home / Other'}
                    name={'phone_number_type'}
                  />
                </Col>
                <Col md={6}>
                  <RadioButton
                    groupValue={formik.values.phone_number_type}
                    value={'cell'}
                    label={'Cell / Mobile'}
                    name={'phone_number_type'}
                  />
                </Col>
              </Form.Group>
            </Col>
          </Row>
          <div className="text-muted my-3">
            Do you have an alternative phone number in case we can't contact you?
          </div>

          <Form.Group
            as={Col}
            className="my-4"
            name={'is_have_alternative_phone_number'}
            values={formik.values.is_have_alternative_phone_number}
            onChange={formik.handleChange}
          >
            <span className="me-3">
              <RadioInputRounded
                isActive={formik.values.is_have_alternative_phone_number === 'true'}
                value={'true'}
                label={'Yes'}
                name={'is_have_alternative_phone_number'}
              />
            </span>
            <span className="me-3">
              <RadioInputRounded
                isActive={formik.values.is_have_alternative_phone_number === 'false'}
                value={'false'}
                label={'No'}
                name={'is_have_alternative_phone_number'}
              />
            </span>
          </Form.Group>
          <Row>
            <Col md={8}>
              {alternativePhone && (
                <Card body className="bg-light border-0 px-1">
                  <div>Alternative Phone Number</div>
                  <Row className="my-1">
                    <SelectPhoneCodeInput
                      label="Country Code"
                      name="alternative_country_code"
                      onChange={formik.handleChange}
                      values={formik.values.alternative_country_code}
                    />
                    <TextIconInput
                      icon={<PhoneAndroidIcon />}
                      name="alternative_phone_number"
                      onChange={formik.handleChange}
                      values={formik.values.alternative_phone_number}
                      label="Phone Number"
                    />
                  </Row>
                  <Form.Group
                    as={Row}
                    style={{ marginTop: '-16px' }}
                    name={'alternative_phone_number_type'}
                    values={formik.values.alternative_phone_number_type}
                    onChange={formik.handleChange}
                  >
                    <Col md={6}>
                      <RadioButton
                        groupValue={formik.values.alternative_phone_number_type}
                        value={'home'}
                        label={'Home / Other'}
                        name={'alternative_phone_number_type'}
                      />
                    </Col>
                    <Col md={6}>
                      <RadioButton
                        groupValue={formik.values.alternative_phone_number_type}
                        value={'cell'}
                        label={'Cell / Mobile'}
                        name={'alternative_phone_number_type'}
                      />
                    </Col>
                  </Form.Group>
                </Card>
              )}
            </Col>
          </Row>

          <div className="fs-5 my-3">
            Can we share your contact information with UC campuses and programs?
          </div>
          <div className="text-muted">
            If you check this option, you may receive information about admissions tips, updates and
            reminders from UC campuses (even ones you haven't applied to) and outreach programs.
            They may contact you before and/or after you've submitted the application.
          </div>
          <div className="my-3">
            <CheckboxInput
              mdCol={'6'}
              label={
                <span className="mt-2 text-primary">
                  I authorize the University of California to share my contact information.
                </span>
              }
              name={'is_allowed_share_contact'}
              values={formik.values.is_allowed_share_contact}
              onChange={formik.handleChange}
            />
          </div>

          <div className="border w-100 my-3" />
          <div className="m-3 d-flex justify-content-end border-top pt-3">
            <div className="col col-md-3">
              <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                {basicInformationStore.status === 'loading' ? (
                  <Spinner size="sm" animation="border" variant="primary" />
                ) : (
                  'Continue'
                )}
              </Button>
            </div>
          </div>
        </Form>
      </Container>
    </div>
  );
};

export default ContactDetailPage;
