import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import React from 'react';
import { Row } from 'react-bootstrap';

interface Props {
  formik: any;
}

const Religious = (props: Props) => {
  const religiousOptions = [
    {
      value: 'Christianity',
    },
    {
      value: 'Islam',
    },
    {
      value: 'Atheist',
    },
    {
      value: 'Agnostic',
    },
    {
      value: 'Hindu',
    },
    {
      value: 'Buddhist',
    },
    {
      value: 'Other',
    },
  ];
  const [otherReligious, setOtherReligious] = React.useState(false);
  React.useEffect(() => {
    if (props.formik.values.religious_preference === 'Other') {
      setOtherReligious(true);
    } else {
      setOtherReligious(false);
    }
  }, [props.formik.values.religious_preference]);
  return (
    <Row className="m-0">
      <div className="fs-5" style={{ marginLeft: '-4px' }}>
        Religious Preference
      </div>
      <SelectInput
        option={religiousOptions}
        mdCol={'6'}
        label={'Religious Preference'}
        name={'religious_preference'}
        values={props.formik.values.religious_preference}
        onChange={props.formik.handleChange}
        feedback={
          props.formik.touched.religious_preference &&
          props.formik.errors.religious_preference && (
            <span className="text-danger">{props.formik.errors.religious_preference}</span>
          )
        }
      />
      {otherReligious && (
        <TextInput
          mdCol={'6'}
          label={'Specify other religion or denomination'}
          name={'other_religion'}
          values={props.formik.values.other_religion}
          onChange={props.formik.handleChange}
          countText={50}
          feedback={
            props.formik.touched.other_religion &&
            props.formik.errors.other_religion && (
              <span className="text-danger">{props.formik.errors.other_religion}</span>
            )
          }
        />
      )}
    </Row>
  );
};

export default Religious;
