import CheckboxInput from 'components/input/CheckboxInput';
import RadioInputRounded from 'components/input/RadioInputRounded';
import SelectInput from 'components/input/SelectInput';
import React from 'react';
import { Row, Col, Form } from 'react-bootstrap';

interface Props {
  formik: any;
  militaryOptions: any[];
}

const Military = (props: Props) => {
  const status = [
    { value: 'None' },
    { value: 'Currently Serving' },
    { value: 'Previously Served' },
    { value: 'Current Dependent' },
  ];
  return (
    <Row className="m-0">
      <div className="fs-5" style={{ marginLeft: '-4px' }}>
        Military Affilliation
      </div>
      <SelectInput
        option={status}
        mdCol={'6'}
        label={'U.S. Armed Forces Status'}
        name={'us_armed_forces_status'}
        values={props.formik.values.us_armed_forces_status}
        onChange={props.formik.handleChange}
        feedback={
          props.formik.touched.us_armed_forces_status &&
          props.formik.errors.us_armed_forces_status && (
            <span className="text-danger">{props.formik.errors.us_armed_forces_status}</span>
          )
        }
      />
      <Col xs={12}>
        <div>Are you a dependent of a U.S. military veteran or service member?</div>
        <Form.Group
          as={Col}
          className="my-2"
          name={'is_dependent_us_military'}
          values={props.formik.values.is_dependent_us_military}
          onChange={props.formik.handleChange}
        >
          <div className="d-inline-block me-1 mb-3">
            <RadioInputRounded
              isActive={props.formik.values.is_dependent_us_military === 'true'}
              value={'true'}
              label={'Yes'}
              name={'is_dependent_us_military'}
            />
          </div>
          <div className="d-inline-block me-1 mb-1">
            <RadioInputRounded
              isActive={props.formik.values.is_dependent_us_military === 'false'}
              value={'false'}
              label={'No'}
              name={'is_dependent_us_military'}
            />
          </div>
        </Form.Group>
        <div>Check all that apply to you: *</div>
        {props.militaryOptions.map((option, index) => {
          const keyName = option.id;
          return (
            <CheckboxInput
              key={index}
              label={<span className="mt-2 text-muted">{option.value}</span>}
              name={option.id}
              values={props.formik.values[keyName]}
              onChange={props.formik.handleChange}
            />
          );
        })}
      </Col>
    </Row>
  );
};

export default Military;
