import React from 'react';
import { Form } from 'react-bootstrap';

interface Props {
  tooltip?: string;
  mdCol?: string;
  label?: any;
  name: string;
  values: any;
  onChange: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
}

const CheckboxInput = (props: Props) => {
  return (
    <Form.Check type={'checkbox'} id={props.name} className="d-flex">
      <Form.Check.Input
        checked={props.values}
        className="me-2 mb-1"
        style={{ padding: '8px' }}
        type={'checkbox'}
        placeholder={props.placeholder}
        name={props.name}
        onChange={props.onChange}
        isValid={props.isValid}
      />
      <Form.Check.Label style={{ marginTop: '2px' }}>{props.label}</Form.Check.Label>
      {props.feedback}
    </Form.Check>
  );
};

export default CheckboxInput;
