import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  mdCol?: string;
  label?: string;
  name: string;
  values: any;
  onChange?: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
  disabled?: boolean;
  countText?: number;
}

const TextInput = (props: Props) => {
  const [counter, setCounter] = React.useState(0);
  return (
    <Form.Group
      name={props.name}
      className="mb-3 p-1 p-md-2"
      as={Col}
      md={props.mdCol || '6'}
      controlId="validationFormik01"
    >
      <Form.Label>{props.label}</Form.Label>
      <Form.Control
        disabled={props.disabled}
        placeholder={props.placeholder}
        type="text"
        name={props.name}
        value={props.values}
        maxLength={props.countText}
        onChange={(e) => {
          props.onChange && props.onChange(e);
          setCounter(e.target.value.length);
        }}
        isValid={props.isValid}
      />
      {props.countText && (
        <div className="sized50 w-100 text-end">
          {counter}/{props.countText}
        </div>
      )}
      {props.feedback}
    </Form.Group>
  );
};

export default TextInput;
