import React from 'react';
import { Form } from 'react-bootstrap';

interface Props {
  name: string;
  label: string;
  value: any;
  defaultChecked?: boolean;
  groupValue?: any;
}

const RadioButton = (props: Props) => {
  const [checkValue, setCheckValue] = React.useState<boolean | undefined>(undefined);
  React.useEffect(
    () => setCheckValue(props.groupValue === props.value),
    [props.groupValue, props.value]
  );
  return (
    <div>
      <Form.Check
        type="radio"
        defaultChecked={checkValue}
        label={props.label}
        name={props.name}
        value={props.value}
        id={props.name + props.value}
      />
      <style scoped>{`
        .form-check-input:checked {
          background-color: #5F30E2 !important;
          border-color: #5F30E2 !important;
        }
      `}</style>
    </div>
  );
};

export default RadioButton;
