import http from "./http"

const basicInformationServices = {
  async getList (params:any) {
    const res = http.get('api/personal_information')
    return res

  },
  async create(body: any): Promise<any> {
    const res = http.post('api/personal_information', body)
    return res
  },
  async getBasicDetail(student_id: any) {
    const res = http.get(`api/personal_information/${student_id}`)
    return res
  },
  async getContactDetail(student_id: any) {
    const res = http.get(`api/contact-details/${student_id}`)
    return res
  },

  
  async updateContact(body:any): Promise<any> {
    const res = http.put('api/contact-details', body)
    return res
  },

  async update(body:any): Promise<any> {
    const res = http.put('api/personal_information', body)
    return res
  },

  async getAllDemoGraphics(): Promise<any> {
    const res = http.get('api/demographics')
    return res
  },

  async getDemoGraphics(student_id: any) {
    const res = http.get(`api/demographics/${student_id}`)
    return res
  },

  async updateDemographics(body:any): Promise<any> {
    const res = http.put('api/demographics', body)
    return res
  },
  async createDemographics(body:any): Promise<any> {
    const res = http.post('api/demographics', body)
    return res
  },
  
}

export default basicInformationServices