import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import React from 'react';
import { Col, Row, Card, Button } from 'react-bootstrap';
import LanguageConfig from './components/LanguageConfig';

interface Props {
  formik: any;
}

export interface languageOptionProps {
  language: String;
  isFirstLanguage: boolean;
  isSpeak: boolean;
  isRead: boolean;
  isWrite: boolean;
  isSpokenAtHome: boolean;
}

const Language = (props: Props) => {
  const languageOptions = [
    { value: 'English Only' },
    { value: 'English and another language' },
    { value: 'Other Language' },
  ];
  const [haveAnotherLanguage, setHaveAnotherLanguage] = React.useState(true);
  const [moreLanguageOption, setMoreLanguageOption] = React.useState<languageOptionProps[]>([]);
  React.useEffect(() => {
    if (props.formik.values.language === 'English Only') {
      setHaveAnotherLanguage(false);
    } else {
      setHaveAnotherLanguage(true);
    }
    setMoreLanguageOption(props.formik.values.language_array);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.formik.values.language]);

  React.useEffect(() => {
    props.formik.setFieldValue('language_array', JSON.stringify(moreLanguageOption));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [moreLanguageOption]);

  return (
    <Row className="m-0">
      <div className="fs-5" style={{ marginLeft: '-4px' }}>
        Language
      </div>
      <SelectInput
        option={languageOptions}
        mdCol={'6'}
        label={'Language'}
        name={'language'}
        values={props.formik.values.language}
        onChange={props.formik.handleChange}
        feedback={
          props.formik.touched.language &&
          props.formik.errors.language && (
            <span className="text-danger">{props.formik.errors.language}</span>
          )
        }
      />
      <Col md={6} className="p-0">
        {haveAnotherLanguage && (
          <Card className="h-100 bg-light border-0">
            <TextInput
              mdCol={'12'}
              label={'Specify other religion language'}
              name={'other_religion_language'}
              values={props.formik.values.other_religion_language}
              onChange={props.formik.handleChange}
              countText={50}
              feedback={
                props.formik.touched.other_religion_language &&
                props.formik.errors.other_religion_language && (
                  <span className="text-danger">{props.formik.errors.other_religion_language}</span>
                )
              }
            />
          </Card>
        )}
      </Col>
      <SelectInput
        option={[{ value: 1 }, { value: 2 }, { value: 3 }, { value: 4 }, { value: 5 }]}
        mdCol={'6'}
        label={'Number of languages you are proficient in*'}
        name={'number_language_proficient'}
        values={props.formik.values.number_language_proficient}
        onChange={props.formik.handleChange}
        feedback={
          props.formik.touched.number_language_proficient &&
          props.formik.errors.number_language_proficient && (
            <span className="text-danger">{props.formik.errors.number_language_proficient}</span>
          )
        }
      />
      <Col xs={12}>
        {(moreLanguageOption || []).map((option, index) => (
          <LanguageConfig
            key={index}
            information={option}
            index={`${index + 1}/${props.formik.values.number_language_proficient}`}
            handleSave={(newMoreLanguage) => {
              const newMoreLanguageOption = [...moreLanguageOption];
              newMoreLanguageOption[index] = newMoreLanguage;
              setMoreLanguageOption(newMoreLanguageOption);
            }}
            handleRemove={() => {
              const newMoreLanguageOption = [...moreLanguageOption];
              newMoreLanguageOption.splice(index, 1);
              setMoreLanguageOption(newMoreLanguageOption);
            }}
          />
        ))}
        <div className="col-6 col-md-3 mx-auto">
          <Button
            variant="primary"
            className="w-100"
            onClick={() => {
              const newMoreLanguageOption: languageOptionProps[] = [
                ...moreLanguageOption,
                {
                  language: 'English',
                  isFirstLanguage: false,
                  isSpeak: false,
                  isRead: false,
                  isWrite: false,
                  isSpokenAtHome: false,
                },
              ];
              setMoreLanguageOption(newMoreLanguageOption);
            }}
            disabled={moreLanguageOption?.length >= props.formik.values.number_language_proficient}
          >
            Add new language
          </Button>
        </div>
      </Col>
    </Row>
  );
};

export default Language;
