import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  label?: string;
  mdCol?: string;
  name?: string;
  values?: any;
  onChange?: (e?: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
}

const SelectPhoneCodeInput = (props: Props) => {
  return (
    <Form.Group
      className="mb-3 p-0 p-md-2"
      name={props.name}
      as={Col}
      md={props.mdCol || '6'}
      controlId="formGridState"
    >
      <Form.Label>{props.label}</Form.Label>
      <Form.Select
        onChange={(values) => {
          console.log(values);
          if (props.onChange) props.onChange(values);
        }}
        name={props.name}
        value={props.values}
        defaultValue={'+1'}
      >
        <option value={'+1'}> +1 United States </option>
        <option value={''}>Other </option>
      </Form.Select>
    </Form.Group>
  );
};

export default SelectPhoneCodeInput;
