import * as React from 'react';
import Menu from '@mui/material/Menu';
import { Button } from 'react-bootstrap';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import Collapse from '@mui/material/Collapse';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { appMenuGroups, icons, routers } from 'router/router';
import { useHistory } from 'react-router';

import DashboardOutlinedIcon from '@mui/icons-material/DashboardOutlined';

export default function MobileMenu() {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const openMenu = Boolean(anchorEl);
  const handleClickButton = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const redirectPage = async (child: any) => {
    setAnchorEl(null);
    history.push(child.path);
  };
  const [open, setOpen] = React.useState<string | null>(null);
  let history = useHistory();
  const handleClick = (route: any) => {
    if (open === route.name) return setOpen(null);
    setOpen(route.name);
  };
  let title = 'Menu';
  return (
    <div>
      <Button variant="outline-dart" onClick={handleClickButton}>
        {title}
      </Button>
      <Menu
        className="w-100"
        anchorEl={anchorEl}
        onClose={handleClose}
        open={openMenu}
        PaperProps={{
          style: {
            width: '100%',
          },
        }}
      >
        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }} component="nav">
          <ListItemButton
            onClick={async () => {
              await redirectPage(routers.dashBoard);
              title = routers.dashBoard.name;
            }}
          >
            <DashboardOutlinedIcon
              className="text-muted"
              style={{ marginLeft: '-12px', marginRight: '4px' }}
            />
            <span className="mr-1 pr-1">Dashboard</span>
          </ListItemButton>
          {appMenuGroups.map((route: any, index: any) => {
            const IconBefore = icons[route.icon] || 'Component';
            return (
              <div key={index}>
                <ListItemButton onClick={() => handleClick(route)} className="px-1">
                  <IconBefore fontSize="small" className="me-2" />
                  <span>{route.name}</span>
                  {route.isGroup &&
                    (open === route.name ? (
                      <ExpandLess fontSize="small" className="text-muted" />
                    ) : (
                      <ExpandMore fontSize="small" className="text-muted" />
                    ))}
                </ListItemButton>
                {route.isGroup && (
                  <Collapse in={open === route.name} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                      {route.children.map((child: any, i: number) => (
                        <ListItemButton
                          key={i}
                          sx={{ pl: 4 }}
                          onClick={async () => {
                            await redirectPage(child);
                            title = child.name;
                          }}
                        >
                          <span>{child.icon}</span>
                          <span className="mr-1 pr-1">{child.name}</span>
                        </ListItemButton>
                      ))}
                    </List>
                  </Collapse>
                )}
              </div>
            );
          })}
        </List>
      </Menu>
    </div>
  );
}
