import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Col, Row, Button } from 'react-bootstrap';
import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import CheckboxInput from 'components/input/CheckboxInput';
import { languageOptionProps } from '../Language';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}

const ExpandMore = styled((props: ExpandMoreProps) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));
interface Props {
  information: languageOptionProps;
  index: string;
  handleSave: (value: languageOptionProps) => void;
  handleRemove: () => void;
}

const LanguageConfig = (props: Props) => {
  const [expanded, setExpanded] = React.useState(false);
  const [languageInformation, setLanguageInformation] = React.useState<languageOptionProps>(
    props.information
  );
  const [fluencyValue, setFluencyValue] = React.useState('');
  React.useEffect(() => {
    setLanguageInformation(props.information);
  }, [props.information]);
  React.useEffect(() => {
    let newFluencyValue = '';
    if (languageInformation.isFirstLanguage === true) {
      newFluencyValue = newFluencyValue + 'First Language/';
    }
    if (languageInformation.isRead === true) {
      newFluencyValue = newFluencyValue + 'Read/';
    }
    if (languageInformation.isSpeak === true) {
      newFluencyValue = newFluencyValue + 'Speak/';
    }
    if (languageInformation.isWrite === true) {
      newFluencyValue = newFluencyValue + 'Write/';
    }
    if (languageInformation.isSpokenAtHome === true) {
      newFluencyValue = newFluencyValue + 'Spoken At Home/';
    }
    setFluencyValue(newFluencyValue.slice(0, -1));
  }, [languageInformation]);
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  const handlePickLanguage = (e: any) => {
    setLanguageInformation({
      ...languageInformation,
      language: e.target.value,
    });
  };
  const pickLanguageOption = [
    { value: 'English' },
    { value: 'Mandarin' },
    { value: 'Hindi' },
    { value: 'Spanish' },
    { value: 'French' },
    { value: 'Arabic' },
    { value: 'Bengali' },
    { value: 'Russian' },
    { value: 'Portuguese' },
    { value: 'Other' },
  ];
  return (
    <Row>
      <Col xs={11}>
        <Card className="w-100 my-3">
          <CardActions
            disableSpacing
            onClick={handleExpandClick}
            sx={{
              backgroundColor: expanded ? '#F5F1FF' : 'none',
              borderBottom: expanded ? '2px solid' : 'none',
              borderBottomColor: expanded ? '#5F30E2' : 'none',
            }}
          >
            <div className="w-100 d-flex justify-content-between">
              <span className="p-1">{languageInformation.language}</span>
              <strong className="text-muted">{props.index}</strong>
            </div>
            <ExpandMore
              expand={expanded}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </ExpandMore>
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <Row>
                <SelectInput
                  option={pickLanguageOption}
                  values={languageInformation.language}
                  mdCol={'6'}
                  label={'Language'}
                  onChange={handlePickLanguage}
                />
                <TextInput
                  mdCol={'6'}
                  label={'Fluency'}
                  name={'fluency'}
                  values={fluencyValue}
                  disabled
                />
                <div className="text-muted">Select your Language Proficiency</div>
                <Col md={3}>
                  <CheckboxInput
                    label="First Language"
                    name="isFirstLanguage"
                    values={languageInformation.isFirstLanguage}
                    onChange={(e) => {
                      setLanguageInformation({
                        ...languageInformation,
                        isFirstLanguage: e.target.checked,
                      });
                    }}
                  />
                </Col>
                <Col md={2}>
                  <CheckboxInput
                    label="Speak"
                    name="isSpeak"
                    values={languageInformation.isSpeak}
                    onChange={(e) => {
                      setLanguageInformation({
                        ...languageInformation,
                        isSpeak: e.target.checked,
                      });
                    }}
                  />
                </Col>
                <Col md={2}>
                  <CheckboxInput
                    label="Read"
                    name="isRead"
                    values={languageInformation.isRead}
                    onChange={(e) => {
                      setLanguageInformation({
                        ...languageInformation,
                        isRead: e.target.checked,
                      });
                    }}
                  />
                </Col>
                <Col md={2}>
                  <CheckboxInput
                    label="Write"
                    name="isWrite"
                    values={languageInformation.isWrite}
                    onChange={(e) => {
                      setLanguageInformation({
                        ...languageInformation,
                        isWrite: e.target.checked,
                      });
                    }}
                  />
                </Col>
                <Col md={3}>
                  <CheckboxInput
                    label="Spoken at Home"
                    name="isSpokenAtHome"
                    values={languageInformation.isSpokenAtHome}
                    onChange={(e) => {
                      setLanguageInformation({
                        ...languageInformation,
                        isSpokenAtHome: e.target.checked,
                      });
                    }}
                  />
                </Col>
                <Col xs={6} md={9} />
                <Col xs={6} md={3} className="p-3">
                  <Button
                    className="w-100"
                    disabled={props.information === languageInformation}
                    onClick={() => {
                      props.handleSave(languageInformation);
                      setExpanded(false);
                    }}
                  >
                    Save
                  </Button>
                </Col>
              </Row>
            </CardContent>
          </Collapse>
        </Card>
      </Col>
      <Col xs={1}>
        <RemoveCircleOutlineIcon
          className="text-muted pointer my-4"
          onClick={() => props.handleRemove()}
        />
      </Col>
    </Row>
  );
};

export default LanguageConfig;
