/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';
import { Form, Button, Container, Row, Col, Card, Spinner } from 'react-bootstrap';
import { useFormik } from 'formik';
import TextInput from 'components/input/textInput';
import TextAreaInput from 'components/input/TextAreaInput';
import RadioInputRounded from 'components/input/RadioInputRounded';
import RadioButton from 'components/input/RadioButton';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { getCurrentBasicDetail, update } from 'app/basicInformationSlice/basicInformationSlice';
import { BoolToString, formatBoolean, formatDate, suffixOptions } from 'components/helper/helper';
import DatePickerInput from 'components/input/DatePicker';
import { basicInformationRules } from './validation';
import SelectInput from 'components/input/SelectInput';
import Cookies from 'js-cookie';
import { useHistory } from 'react-router';
import { routers } from 'router/router';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';

interface Props {}
const BasicInformationPage = (props: Props) => {
  const history = useHistory();
  const [process, setProcess] = React.useState<number>(0);
  const [anotherMailingAddress, setAnotherMailingAddress] = React.useState(false);
  const dispatch = useAppDispatch();
  const basicInformationStore = useAppSelector((store) => store.basicInformation);
  const studentId = Cookies.get('userId');
  const getDetail = async () => {
    let res = await dispatch(getCurrentBasicDetail(studentId));
    if (res.payload) {
      const dataLoaded: any = res.payload;
      const formValues = {
        address: dataLoaded.address,
        address_line_1: dataLoaded.address_line_1,
        address_line_2: dataLoaded.address_line_2,
        alternate_address: dataLoaded.alternate_address,
        alternate_address_line_1: dataLoaded.alternate_address_line_1,
        alternate_address_line_2: dataLoaded.alternate_address_line_2,
        alternate_city: dataLoaded.alternate_city,
        alternate_state: dataLoaded.alternate_state,
        alternate_zip_code: dataLoaded.alternate_zip_code,
        city: dataLoaded.city,
        dob: dataLoaded.dob,
        email: dataLoaded.email,
        first_name: dataLoaded.first_name,
        gender_identity_description: dataLoaded.gender_identity_description,
        is_have_alternate_address: BoolToString(dataLoaded.is_have_alternate_address),
        is_have_legal_name: BoolToString(dataLoaded.is_have_legal_name),
        is_should_send_mail: BoolToString(dataLoaded.is_should_send_mail),
        is_temporary_address: BoolToString(dataLoaded.is_temporary_address),
        label: dataLoaded.label,
        last_name: dataLoaded.last_name,
        middle_name: dataLoaded.middle_name,
        nick_name: dataLoaded.nick_name,
        other_name: dataLoaded.other_name,
        sex: dataLoaded.sex,
        sex_self_consider: dataLoaded.sex_self_consider,
        sex_self_describe: dataLoaded.sex_self_describe,
        state: dataLoaded.state,
        temporary_from_date: dataLoaded.temporary_from_date,
        temporary_to_date: dataLoaded.temporary_to_date,
        zip_code: dataLoaded.zip_code,
      };
      formik.setValues(formValues);
    }
  };
  const formik = useFormik({
    initialValues: {
      address: '',
      address_line_1: '',
      address_line_2: '',
      alternate_address: '',
      alternate_address_line_1: '',
      alternate_address_line_2: '',
      alternate_city: '',
      alternate_state: '',
      alternate_zip_code: '',
      city: '',
      dob: '',
      email: '',
      first_name: '',
      gender_identity_description: '',
      is_have_alternate_address: 'false',
      is_have_legal_name: 'true',
      is_should_send_mail: 'true',
      is_temporary_address: '',
      label: '',
      last_name: '',
      middle_name: '',
      nick_name: '',
      other_name: '',
      sex: 'female',
      sex_self_consider: 'straight',
      sex_self_describe: 'female',
      state: '',
      temporary_from_date: '',
      temporary_to_date: '',
      zip_code: '',
    },
    validationSchema: basicInformationRules,
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        is_have_alternate_address: formatBoolean(values.is_have_alternate_address),
        is_have_legal_name: formatBoolean(values.is_have_legal_name),
        is_should_send_mail: formatBoolean(values.is_should_send_mail),
        is_temporary_address: formatBoolean(values.is_temporary_address),
        dob: formatDate(values.dob),
        temporary_from_date: formatDate(values.temporary_from_date),
        temporary_to_date: formatDate(values.temporary_to_date),
        student_id: parseInt(studentId || ''),
      };

      const responese = await dispatch(update(newValues));
      if (responese.payload) history.push(routers.contactDetail.path);
    },
  });

  useEffect(() => {
    getDetail();
  }, []);

  useEffect(() => {
    if (formik.values.zip_code?.length) {
      setProcess(99);
    } else {
      setProcess(1);
    }
  }, [formik.values]);
  useEffect(() => {
    if (formik.values.is_should_send_mail === 'false') {
      setAnotherMailingAddress(true);
    } else {
      setAnotherMailingAddress(false);
    }
  }, [formik.values.is_should_send_mail]);
  return (
    <>
      <TopProcessBar
        stepProcess={[
          { title: 'General', percentOnProcess: 35 },
          { title: 'Gender', percentOnProcess: 60 },
          { title: 'Address', percentOnProcess: 85 },
        ]}
        title="Personal Information"
        process={process}
        icon={<PersonOutlineOutlinedIcon />}
      />
      <Container>
        <div className="d-flex justify-content-center">
          <Form style={{ width: '1000px' }} onSubmit={formik.handleSubmit}>
            <div className="text-muted" style={{ fontWeight: 'bolder', fontSize: '1.5rem' }}>
              Basic Information
            </div>
            <Row className="mb-1 m-0">
              <TextInput
                mdCol={'6'}
                label={'First/given name*'}
                name={'first_name'}
                values={formik.values.first_name}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.first_name &&
                  formik.errors.first_name && (
                    <span className="text-danger">{formik.errors.first_name}</span>
                  )
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Middle name'}
                name={'middle_name'}
                values={formik.values.middle_name}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.middle_name &&
                  formik.errors.middle_name && (
                    <span className="text-danger">{formik.errors.middle_name}</span>
                  )
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Last/family/surname*'}
                name={'last_name'}
                values={formik.values.last_name}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.last_name &&
                  formik.errors.last_name && (
                    <span className="text-danger">{formik.errors.last_name}</span>
                  )
                }
              />
              <SelectInput
                option={suffixOptions}
                mdCol={'6'}
                label={'Suffix'}
                name={'label'}
                values={formik.values.label}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.label &&
                  formik.errors.label && <span className="text-danger">{formik.errors.label}</span>
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Prefered Name (Nickname)'}
                name={'nick_name'}
                values={formik.values.nick_name}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.nick_name && formik.errors.nick_name ? (
                    <span className="text-danger">{formik.errors.nick_name}</span>
                  ) : (
                    <span className="text-muted sized70">
                      (If different than Legal First Name))
                    </span>
                  )
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Other Names 🎓'}
                name={'other_name'}
                values={formik.values.other_name}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.other_name && formik.errors.other_name ? (
                    <span className="text-danger">{formik.errors.other_name}</span>
                  ) : (
                    <span className="text-muted sized70">(Records May Be Under)</span>
                  )
                }
              />
              <DatePickerInput
                mdCol={'6'}
                label={'Date of Birth*'}
                name={'dob'}
                placeholder={'Hahaha'}
                values={formik.values.dob}
                onChange={(value) => formik.setFieldValue('dob', value)}
                feedback={
                  formik.touched.dob &&
                  formik.errors.dob && <span className="text-danger">{formik.errors.dob}</span>
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Email*'}
                name={'email'}
                values={formik.values.email}
                onChange={formik.handleChange}
                feedback={
                  formik.touched.email && formik.errors.email ? (
                    <span className="text-danger">{formik.errors.email}</span>
                  ) : (
                    <span className="text-muted sized70">
                      Go to your settings to update your email
                    </span>
                  )
                }
              />
            </Row>
            <div className="text-muted">Have you ever had a different legal name? 🎓</div>
            <div className="text-muted">
              For example, a maiden name or a legal name change. We ask this question so we can
              match your academic records, transcripts and test scores to your application
            </div>
            <Form.Group
              as={Col}
              className="my-4"
              name={'is_have_legal_name'}
              values={formik.values.is_have_legal_name}
              onChange={formik.handleChange}
            >
              <span className="me-3">
                <RadioInputRounded
                  isActive={formik.values.is_have_legal_name === 'true'}
                  value={'true'}
                  label={'Yes'}
                  name={'is_have_legal_name'}
                />
              </span>
              <span className="me-3">
                <RadioInputRounded
                  isActive={formik.values.is_have_legal_name === 'false'}
                  value={'false'}
                  label={'No'}
                  name={'is_have_legal_name'}
                />
              </span>
            </Form.Group>
            <div className="w-100 border my-5" />
            <div>Gender and Sexual Orientation?</div>
            <Form.Group
              as={Col}
              className="my-4"
              name={'sex'}
              values={formik.values.sex}
              onChange={formik.handleChange}
            >
              <div> Sex* 🎓</div>

              <span className="me-3">
                <RadioInputRounded
                  isActive={formik.values.sex === 'female'}
                  value={'female'}
                  label={'Female'}
                  name={'sex'}
                />
              </span>
              <span className="me-3">
                <RadioInputRounded
                  isActive={formik.values.sex === 'male'}
                  value={'male'}
                  label={'Male'}
                  name={'sex'}
                />
              </span>
            </Form.Group>
            <TextAreaInput
              mdCol={'12'}
              tooltip="If you would like the opportunity, we invite you to share more about your gender identity below."
              name={'gender_identity_description'}
              values={formik.values.gender_identity_description}
              onChange={formik.handleChange}
              placeholder="Your Answer"
              feedback={
                formik.touched.gender_identity_description &&
                formik.errors.gender_identity_description && (
                  <span className="text-danger">{formik.errors.gender_identity_description}</span>
                )
              }
            />
            <Form.Group
              as={Col}
              className="my-4"
              name={'sex1'}
              values={formik.values.sex_self_describe}
              onChange={formik.handleChange}
            >
              <div className="text-muted"> How do you describe yourself? 🎓</div>
              <Row className="m-0">
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'female'}
                    label={'Female'}
                    name={'sex_self_describe'}
                  />
                </Col>
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'transMan'}
                    label={'Trans Male/ Trans Man'}
                    name={'sex_self_describe'}
                  />
                </Col>
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'identity'}
                    label={'Different Identity'}
                    name={'sex_self_describe'}
                  />
                </Col>
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'male'}
                    label={'Male'}
                    name={'sex_self_describe'}
                  />
                </Col>
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'tranFemale'}
                    label={'Trans Female/ Trans Woman'}
                    name={'sex_self_describe'}
                  />
                </Col>
                <Col md="4">
                  <RadioButton
                    groupValue={formik.values.sex_self_describe}
                    value={'nonGender'}
                    label={'Genderqueer or Nonbinary Gender'}
                    name={'sex_self_describe'}
                  />
                </Col>
              </Row>
            </Form.Group>
            <Form.Group
              as={Col}
              className="my-4"
              name={'sex_self_consider'}
              values={formik.values.sex_self_consider}
              onChange={formik.handleChange}
            >
              <div className="text-muted"> What do you consider yourself to be? 🎓</div>
              <Row className="m-0">
                <Col md="3">
                  <RadioButton
                    groupValue={formik.values.sex_self_consider}
                    value={'straight'}
                    label={'Heterosexual or straight'}
                    name={'sex_self_consider'}
                  />
                </Col>
                <Col md="3">
                  <RadioButton
                    groupValue={formik.values.sex_self_consider}
                    value={'gayLesian'}
                    label={'Gay or lesbian'}
                    name={'sex_self_consider'}
                  />
                </Col>
                <Col md="3">
                  <RadioButton
                    groupValue={formik.values.sex_self_consider}
                    value={'bisexual'}
                    label={'Bisexual'}
                    name={'sex_self_consider'}
                  />
                </Col>
                <Col md="3">
                  <RadioButton
                    groupValue={formik.values.sex_self_consider}
                    value={'notListed'}
                    label={'Not listed above'}
                    name={'sex_self_consider'}
                  />
                </Col>
              </Row>
            </Form.Group>
            <div className="w-100 border my-5" />
            <div>What is your home address?</div>
            <div className="text-muted sized70">
              This address is your permanent address. It’s most likely where your parents or legal
              guardians live. If you’re an international student, please enter your home country
              address.
            </div>
            <Row className="my-1 m-0">
              <TextInput
                mdCol={'12'}
                label={'Your Address'}
                name={'address'}
                values={formik.values.address}
                onChange={formik.handleChange}
                feedback={
                  <span className="text-muted sized70">
                    Write down your address here, and we will do the rest for you
                  </span>
                }
              />
              <TextInput
                mdCol={'6'}
                label={'Address Line 1 (including Apartnemt or Suite Number)*'}
                name={'address_line_1'}
                values={formik.values.address_line_1}
                onChange={formik.handleChange}
              />
              <TextInput
                mdCol={'6'}
                label={'Address Line 2'}
                name={'address_line_2'}
                values={formik.values.address_line_2}
                onChange={formik.handleChange}
              />
              <TextInput
                mdCol={'6'}
                label={'City*'}
                name={'city'}
                values={formik.values.city}
                onChange={formik.handleChange}
              />
              <TextInput
                mdCol={'6'}
                label={'State*'}
                name={'state'}
                values={formik.values.state}
                onChange={formik.handleChange}
              />
              <TextInput
                mdCol={'6'}
                label={'Zip Code'}
                name={'zip_code'}
                values={formik.values.zip_code}
                onChange={formik.handleChange}
              />
              <Col md="6"></Col>
              <Col md="6">
                <div className="text-muted">Is this where we should send mail?</div>
                <div className="text-muted">
                  UC's need your current mailing address to send mail correspondences about the
                  admissions process
                </div>
                <Form.Group
                  as={Col}
                  className="my-4"
                  name={'is_should_send_mail'}
                  values={formik.values.is_should_send_mail}
                  onChange={formik.handleChange}
                >
                  <span className="me-3">
                    <RadioInputRounded
                      isActive={formik.values.is_should_send_mail === 'true'}
                      value={'true'}
                      label={'Yes'}
                      name={'is_should_send_mail'}
                    />
                  </span>
                  <span className="me-3">
                    <RadioInputRounded
                      isActive={formik.values.is_should_send_mail === 'false'}
                      value={'false'}
                      label={'No'}
                      name={'is_should_send_mail'}
                    />
                  </span>
                </Form.Group>
              </Col>
              <Col md="6">
                <div className="text-muted">Do you have an alternate mailing address?</div>
                <div className="text-muted">
                  If you attend a boarding school or use a different mailing address
                </div>
                <Form.Group
                  as={Col}
                  className="my-4"
                  name={'alternateMailing'}
                  values={formik.values.is_have_alternate_address}
                  onChange={formik.handleChange}
                >
                  <div className="d-inline-block me-1 mb-3">
                    <RadioInputRounded
                      isActive={formik.values.is_have_alternate_address === 'false'}
                      value={'false'}
                      label={'No alternate address'}
                      name={'is_have_alternate_address'}
                    />
                  </div>
                  <div className="d-inline-block me-1 mb-1">
                    <RadioInputRounded
                      isActive={formik.values.is_have_alternate_address === 'true'}
                      value={'true'}
                      label={'Send mail to Mailing Address'}
                      name={'is_have_alternate_address'}
                    />
                  </div>
                </Form.Group>
              </Col>
              <Col md="6"></Col>
            </Row>

            {anotherMailingAddress && (
              <Card body className="bg-light border-0">
                <div>Mailing Address</div>

                <Row className="my-1 m-0">
                  <TextInput
                    mdCol={'12'}
                    label={'Your Address'}
                    name={'alternate_address'}
                    values={formik.values.alternate_address}
                    onChange={formik.handleChange}
                    feedback={
                      <span className="text-muted sized70">
                        Write down your address here, and we will do the rest for you
                      </span>
                    }
                  />
                  <TextInput
                    mdCol={'6'}
                    label={'Address Line 1 (including Apartnemt or Suite Number)'}
                    name={'alternate_address_line_1'}
                    values={formik.values.alternate_address_line_1}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'6'}
                    label={'Address Line 2'}
                    name={'alternate_address_line_2'}
                    values={formik.values.alternate_address_line_2}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'6'}
                    label={'City'}
                    name={'alternate_city'}
                    values={formik.values.alternate_city}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'6'}
                    label={'State'}
                    name={'alternate_state'}
                    values={formik.values.alternate_state}
                    onChange={formik.handleChange}
                  />
                  <TextInput
                    mdCol={'6'}
                    label={'Zip Code'}
                    name={'alternate_zip_code'}
                    values={formik.values.alternate_zip_code}
                    onChange={formik.handleChange}
                  />
                  <Col md="6"></Col>
                  <Col md="12">
                    <div>Is this a temporary address?</div>
                    <Form.Group
                      as={Col}
                      className="my-4"
                      name={'is_temporary_address'}
                      values={formik.values.is_temporary_address}
                      onChange={formik.handleChange}
                    >
                      <span className="me-3">
                        <RadioInputRounded
                          isActive={formik.values.is_temporary_address === 'true'}
                          value={'true'}
                          label={'Yes'}
                          name={'is_temporary_address'}
                        />
                      </span>
                      <span>
                        <RadioInputRounded
                          isActive={formik.values.is_temporary_address === 'false'}
                          value={'false'}
                          label={'No'}
                          name={'is_temporary_address'}
                        />
                      </span>
                    </Form.Group>
                  </Col>
                  <DatePickerInput
                    mdCol="6"
                    label="From date"
                    name="temporary_from_date"
                    values={formik.values.temporary_from_date}
                    onChange={(value) => formik.setFieldValue('temporary_from_date', value)}
                  />
                  <DatePickerInput
                    mdCol="6"
                    label="To date"
                    name="temporary_to_date"
                    values={formik.values.temporary_to_date}
                    onChange={(value) => formik.setFieldValue('temporary_to_date', value)}
                  />
                </Row>
              </Card>
            )}
            <div className="m-3 d-flex justify-content-end border-top pt-3">
              <div className="col col-md-3">
                <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                  {basicInformationStore.status === 'loading' ? (
                    <Spinner size="sm" animation="border" variant="primary" />
                  ) : (
                    'Continue'
                  )}
                </Button>
              </div>
            </div>
          </Form>
        </div>
      </Container>
    </>
  );
};

export default BasicInformationPage;
